// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const dateNow = new Date();

const Demo = function () {
  return (
    <div>
      <h2>Selected Date</h2>
      <Datepicker
        placeholder={'点击选择日期'}
        selected={dateNow}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo3')
);
